


































import {Component, Mixins, Prop, Watch} from "vue-property-decorator";
import att_mixins from "@/mixins/basic";

@Component({components: {}})
export default class edit_info extends Mixins(att_mixins) {

    edit: any = {}

    @Prop({
        required: true,
        type: Boolean,
        default: false
    }) value !: boolean

    @Prop({
        required: true,
        type: Object,
        default: false
    }) user_info !: {}

    @Watch('value')
    get_visible(vl: boolean) {
        this.is_open = vl
    }

    @Watch('user_info')
    get_edit(vl: any) {
        this.edit = vl
    }

    edit_user_Info() {
        this.$http.put(`${this.$config.url}/manage_user`, {
            'username': this.edit.username,
            'rule': this.edit.rule,
            'department': this.edit.department,
            'real_name': this.edit.real_name,
            'mail': this.edit.email,
            'tp': 'info'
        })
            .then((res: { data: string; }) => {
                this.$config.notice(res.data);
                this.resetFields('edit_user')
                this.$emit("call")
            })
            .catch((error: any) => {
                this.$config.err_notice(this, error)
            });
        this.cancel()
    }
}
