


































































































import '../../../styles/tablesmargintop.css'
import edit_password from "@/components/modal/edit_password.vue";
import {Mixins, Component} from "vue-property-decorator";
import att_mixins from "@/mixins/basic";
import edit_rule from "@/components/modal/edit_rule.vue";
import edit_info from "@/components/modal/edit_info.vue";
import module_verify from "@/store/modules/verify";
import module_user from "@/store/modules/user";
// eslint-disable-next-line no-unused-vars
import {DependUser} from "@/views/management/user/types";

@Component({components: {edit_password, edit_rule, edit_info}})
export default class user_info extends Mixins(att_mixins) {

    regExp_password = (rule: any, value: string, callback: any) => {
        let pPattern = /^.*(?=.{6,})(?=.*\d)(?=.*[A-Z])(?=.*[a-z]).*$/;
        if (!pPattern.test(value)) {
            callback(new Error('至少1个大写字母,1个小写字母,1个数字'))
        } else {
            callback()
        }
    };

    valideuserinfoPassword = (rule: any, value: string, callback: any) => {
        if (value !== this.userinfo.password) {
            callback(new Error('两次输入密码不一致'))
        } else {
            callback()
        }
    };

    is_depend = false

    is_edit = false

    columns = [
        {
            title: '用户名',
            key: 'username',
            sortable: true
        },
        {
            title: '角色',
            key: 'rule',
            sortable: true,
            slot: 'rule'
        },
        {
            title: '姓名',
            key: 'real_name',
            sortable: true
        },
        {
            title: '部门',
            key: 'department',
            sortable: true
        },
        {
            title: 'email',
            key: 'email',
            sortable: true
        },
        {
            title: '操作',
            key: 'action',
            width: 300,
            align: 'center',
            slot: 'action'
        }
    ];
    // 新建用户
    userinfo = {
        username: '',
        password: '',
        confirm_password: '',
        group: '',
        checkbox: '',
        department: '',
        mail: '',
        real_name: ''
    };
    userInfoValidate = {
        username: [{
            required: true,
            message: '请输入用户名',
            trigger: 'blur'
        }],
        password: [
            {
                required: true,
                message: '请输入密码',
                trigger: 'blur'
            },
            {
                min: 6,
                message: '请至少输入6个字符',
                trigger: 'blur'
            },
            {
                max: 32,
                message: '最多输入32个字符',
                trigger: 'blur'
            },
            {
                validator: this.regExp_password,
                trigger: 'blur'
            }
        ],
        confirm_password: [
            {
                required: true,
                message: '请再次输入新密码',
                trigger: 'blur'
            },
            {
                validator: this.valideuserinfoPassword,
                trigger: 'blur'
            }
        ],
        group: [
            {
                required: true,
                message: '请输入权限',
                trigger: 'blur'
            }
        ],
        department: [
            {
                required: true,
                message: '请输入部门名称',
                trigger: 'blur'
            },
            {
                min: 2,
                message: '请至少输入2个字符',
                trigger: 'blur'
            },
            {
                max: 32,
                message: '最多输入32个字符',
                trigger: 'blur'
            }
        ],
        real_name: [
            {
                required: true,
                message: '请输入姓名',
                trigger: 'blur'
            },
            {
                min: 2,
                message: '请至少输入2个字符',
                trigger: 'blur'
            },
            {
                max: 32,
                message: '最多输入32个字符',
                trigger: 'blur'
            }],
        mail: [
            {
                required: true,
                message: '请输入工作邮箱',
                trigger: 'blur'
            },
            {
                min: 2,
                message: '请至少输入2个字符',
                trigger: 'blur'
            },
            {
                max: 32,
                message: '最多输入32个字符',
                trigger: 'blur'
            }]
    };

    // 用户名
    username = '';

    del_disabled = true

    payload = {} as any

    depend_list = {} as DependUser

    edit_code(row: { username: string }) {
        this.edit_password = true;
        module_user.fetch_user_info(row)
    }

    edit_user(row: any) {
        this.payload = JSON.parse(JSON.stringify(row))
        this.payload.multi = this.connectionList.multi
        this.is_edit = true;
    }

    edit_rule(row: { username: string; }) {
        this.$http.post(`${this.$config.url}/manage_user/fetch/group`, {
            username: row.username
        })
            .then((res: { data: { group_list: never[]; group: never[]; }; }) => {
                let group = JSON.parse(JSON.stringify(res.data))
                group.username = row.username
                module_verify.fetch_user_permissions(group)
            })
            .catch((err: any) => this.$config.err_notice(this, err))
            .finally(() => this.is_open = true)
    }

    registered() {
        let is_validate: any = this.$refs['userinfova'];
        is_validate.validate((valid: boolean) => {
            if (valid) {
                this.loading = true;
                this.$http.post(`${this.$config.url}/manage_user`, {
                    'user_info': this.userinfo
                })
                    .then((res: { data: string; }) => {
                        this.$config.notice(res.data);
                    })
                    .catch((error: any) => {

                        this.$config.err_notice(this, error)
                    })
                    .finally(() => {
                        this.current_page(this.current);
                        is_validate.resetFields()
                        this.loading = !this.loading;
                    })
            }
        })
    }

    current_page(vl = 1) {
        this.$http.put(`${this.$config.url}/manage_user/fetch`,{
            page: vl,
            query: this.query
        })
            .then((res: { data: { multi: any; data: never[]; page: number; }; }) => {
                this.connectionList.multi = res.data.multi;
                this.table_data = res.data.data;
                this.page_number = res.data.page;
            })
            .catch((error: any) => {
                this.$config.err_notice(this, error)
            })
    }

    show_depend(row: { username: string; }) {
        this.is_depend = !this.is_depend
        this.del_disabled = true
        this.username = row.username
        this.$http.get(`${this.$config.url}/manage_user/depend?user=${row.username}`)
            .then((res: { data: DependUser }) => {
                this.depend_list = res.data
            })
            .catch((err: any) => this.$config.err_notice(this, err))
            .finally(() => {
                if (this.depend_list.grained.length === 0 && this.depend_list.source.length === 0) {
                    this.del_disabled = !this.del_disabled
                }
            })
    }

    delUser() {
        let step = this.current;
        if (this.table_data.length === 1) {
            step = step - 1
        }
        this.$http.delete(`${this.$config.url}/manage_user?user=${this.username}`)
            .then((res: { data: string; }) => {
                this.$config.notice(res.data)
            })
            .catch((error: any) => {
                this.$config.err_notice(this, error)
            })
            .finally(() => {
                this.current_page(step)
                this.is_depend = !this.is_depend
            })
    }

    queryData() {
        this.query.valve = true;
        this.current_page()
    }

    queryCancel() {
        this.query.valve = false;
        this.current_page()
    }

    mounted() {
        this.current_page();
    }
}
